import React, { Component } from 'react';
import './style.css'

class Prompt extends Component {
  render() {
    const { command, theme } = this.props;
    return (
      <p className={theme.darkTheme ? "prompt" : "prompt alt"}><span>user@ek:~$</span> {command}</p>
    );
  }
}

export default Prompt;



