import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter } from 'react-router-dom'
import { createStore, applyMiddleware } from 'redux'
import { Provider } from 'react-redux'
import thunk from 'redux-thunk'
import { composeWithDevTools } from 'redux-devtools-extension'
import reducer from './redux/rootReducer'
import { ThemeToggled } from './redux/theme/action'
import './index.css';
import App from './App';
import registerServiceWorker from './registerServiceWorker';

const store = createStore(
  reducer,
  composeWithDevTools(applyMiddleware(thunk))
)

// due to the lack of proper backend temporarily storing theme in localStorage
if (localStorage.darkTheme && localStorage.darkTheme.length > 0){
  store.dispatch(ThemeToggled(localStorage.darkTheme === 'true'))
}

ReactDOM.render((
  <BrowserRouter basename={process.env.PUBLIC_URL}>
    <Provider store={store}>
      <App />
    </Provider>
  </BrowserRouter>
), document.getElementById('root'));
registerServiceWorker();



