import * as ActionTypes from './types'

export default function theme(state = {'darkTheme': 0, 'menuOpen': 0, 'trianglesState': 0}, action = {}) {
  switch (action.type) {
    case ActionTypes.MENU_OPENED:
      return {
        ...state,
        menuOpen: 1
      }
    case ActionTypes.MENU_CLOSED:
      return {
        ...state,
        menuOpen: 0
      }
    case ActionTypes.DARK_THEME_SELECTED:
      return {
        ...state,
        darkTheme: 1
      }
    case ActionTypes.LIGHT_THEME_SELECTED:
      return {
        ...state,
        darkTheme: 0
      }
    case ActionTypes.TRIANGLES_ACTIVATED:
      return {
        ...state,
        trianglesState: 1
      }
    case ActionTypes.TRIANGLES_DEACTIVATED:
      return {
        ...state,
        trianglesState: 0
      }
    default:
      return state;
  }
}



