import React, { Component } from 'react';
import { connect } from 'react-redux'
import { toggleTheme, toggleMenu, toggleTriangles } from '../../redux/theme/action'
import './style.css'

class Menu extends Component {
  onMenuToggle = (data) => {
    this.props.toggleMenu(data)
  }
  onThemeChange = (data) => {
    this.props.toggleTheme(data)
  }
  onTrianglesChange = (data) => {
    this.props.toggleTriangles(data)
  }

  componentDidMount() {
    if (!!localStorage.getItem('darkTheme')){
      this.props.toggleTheme(localStorage.getItem('darkTheme') === 'true')
    } else {
      this.props.toggleTheme(localStorage.getItem(false))
    }
  }

  render() {
    const { theme } = this.props;
    return (
      <div className={theme.darkTheme ? "menu" : "menu alt"}>
        {
          !!theme.menuOpen &&
          <ul className="list">
            <li><a {...(window.location.pathname === '/' ? { 'href': '#' } : { 'href': '/' })}>Home</a></li>
            <li><a {...(window.location.pathname === '/achievements' ? { 'href': '#' } : { 'href': '/achievements' })}>Achievements</a></li>
            <li className="toggleHolder" onClick={this.onThemeChange.bind(this, !theme.darkTheme)}>Change Theme ({theme.darkTheme ? "Dark" : "Light"})</li>
            {
              window.location.pathname === '/achievements' &&
              <li className="toggleHolder" onClick={this.onTrianglesChange.bind(this, !theme.trianglesState)}>Triangular layout: {theme.trianglesState ? "On" : "Off"}</li>
            }
          </ul>
        }
        <div className="toggle" onClick={this.onMenuToggle.bind(this, !theme.menuOpen)}>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    theme: state.theme
  }
}

export default connect (mapStateToProps, { toggleTheme, toggleMenu, toggleTriangles })(Menu);



