import React, { Component } from 'react';
import './style.css'
import SheridanPic from './assets/sheridan.png'
import TSLPic from './assets/tsl.svg';
import CICPic from './assets/cic.png';
import CICShadedPic from './assets/cicShaded.png';
import EmeritusPic from './assets/emeritus.png';
import LockPic from './assets/open.svg';

const pics = [SheridanPic, TSLPic, CICPic, EmeritusPic, LockPic, CICShadedPic];

class Card extends Component {
  render() {
    const { content, pic, theme } = this.props;
    const cardClass = this.mapCardClass(content, theme);
    return (
      <div className={cardClass}>
        <div className="img-holder">
          <img src={pics[pic]} alt={content.header}/>
        </div>
        <h4>{content.header}</h4>
        <p className="subheader"><span className="date">{content.date}</span><span>{content.subheader}</span></p>
      </div>
    );
  }
  mapCardClass(content, theme) {
    const themeClass = theme.darkTheme ? '' : 'alt';
    const timeClass = content.type ? content.type : '';
    const extraClass = [themeClass, timeClass].filter(elem => elem.length>0).join(' ');
    const baseClass = extraClass.length > 0 ? 'card ': 'card';
    return baseClass + extraClass;
  }
}

export default Card;



