import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux'
import logo from '../../logo.png';
import Prompt from '../../components/Prompt/'
import Menu from '../../components/Menu/'
import './style.css'

class Home extends Component {
  render() {
    const { theme } = this.props;
    return (
      <div className="home page">
        <Prompt command="whoami" theme={theme}/>
        <div className="profile">
          <div className={theme.darkTheme ? "name" : "name alt"}>
            <img src={logo} alt="logo" />
            <h2>Efim Krakhalev (EK)</h2>
            <h3>Front-end Developer</h3>
            <p>Location: Oakville, ON</p>
            <p>Status: Actively looking for a job</p>
          </div>
          <div className={theme.darkTheme ? "silhouette" : "silhouette alt"}>
          </div>
        </div>
        <Menu />
      </div>
    );
  }
}

Home.propTypes = {
  theme: PropTypes.object.isRequired
}

function mapStateToProps(state) {
  return {
    theme: state.theme
  }
}

export default connect (mapStateToProps)(Home);



