import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux'
import Menu from '../../components/Menu/'
import Prompt from '../../components/Prompt/';
import Card from '../../components/Card/';
import './style.css'

class Achievements extends Component {

  render() {
    const { theme } = this.props;
    const cards = [{
        'header': 'Graduated from Sheridan College',
        'subheader': 'Software Engineering Diploma',
        'date': '2016'
      },
      {
        'header': 'Internship @ The Silverlogic',
        'subheader': 'Front-end development',
        'date': '2016-2017'
      },
      {
        'header': 'Eligible to work in Canada',
        'subheader': 'Post-grad work visa',
        'date': '2016-2019'
      },
      {
        'header': 'Employment @ Emeritus Financial',
        'subheader': 'Front-end development',
        'date': '2017-2018'
      },
      {
        'header': 'You could be here',
        'subheader': '',
        'type': 'future'
      },
      {
        'header': 'Eligible to apply for residency',
        'subheader': 'Requires IELTS',
        'date': 'Approx 2019',
        'type': 'locked'
      }
    ]
    return (
      <div className = { theme.trianglesState ? "achievements page triangular" : "achievements page" } >
        <Prompt command = "ls /usr/achievements" theme = { theme } />
        <div className = { theme.trianglesState ? "cards-holder triangular" : "cards-holder" } >
          {
            cards.map((item, index) => <Card content = { item } pic = { index } key = { index } theme = { theme } />)
          }
        </div>
        <Menu />
      </div>
      );
    }
  }

  Achievements.propTypes = {
    theme: PropTypes.object.isRequired
  }

  function mapStateToProps(state) {
    return {
      theme: state.theme
    }
  }

  export default connect(mapStateToProps)(Achievements);



