import * as ActionTypes from './types'

export const MenuToggled = (menuOpen) => {
  return {
    type: menuOpen ? ActionTypes.MENU_OPENED : ActionTypes.MENU_CLOSED
  }
}

export const ThemeToggled = (darkTheme) => {
  localStorage.setItem('darkTheme', darkTheme)
  return {
    type: darkTheme ? ActionTypes.DARK_THEME_SELECTED : ActionTypes.LIGHT_THEME_SELECTED
  }
}

export const TrianglesToggled = (trianglesState) => {
  return {
    type: trianglesState ? ActionTypes.TRIANGLES_ACTIVATED : ActionTypes.TRIANGLES_DEACTIVATED
  }
}

export const toggleMenu = (menuStatus) => (dispatch) => dispatch(MenuToggled(menuStatus))

export const toggleTheme = (themeStatus) => (dispatch) => dispatch(ThemeToggled(themeStatus))

export const toggleTriangles = (trianglesStatus) => (dispatch) => dispatch(TrianglesToggled(trianglesStatus))



